/*For Pie & Multi Background Colors */
[cell-pi^="[-pi"] {
  display: flex;
  justify-content: center;
  align-items: center;
}

[cell-pi^="[-in"] {
  background-color: white;
}

/*For Progress Bar */
[cell-pb^="[-pa"] {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  align-items: stretch;
}

[cell-pb^="[-ba"] {
  flex-grow: 0;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-lt"] {
  display: flex;
  flex-flow: column wrap;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-lb"] {
  display: flex;
  flex-flow: column-reverse wrap;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-ll"] {
  display: flex;
  align-items: center;
  flex-flow: row wrap;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-ll"] label {
  margin-right: 15px;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-lr"] {
  display: flex;
  align-items: center;
  flex-flow: row-reverse wrap;
}

[cell-pb^="[-pb"][cell-pb$="]"][cell-pb*="-lr"] label {
  margin-left: 15px;
}

/*For Roll Menu*/
[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] {
  display: flex;
}

[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] .icon {
  width: 35px;
  height: 35px;
  background-color: #231977;
  border-radius: 5px;
}

[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] .icon i {
  color: white;
  position: absolute;
  margin-top: 9px;
  margin-left: 10px;
}

[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] .icon .plus {
  display: block;
}

[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] .icon .minus {
  display: none;
}

[cell-rm^="[-rm"] [cell-rm^="[-ra]"] > [cell-rm^="[-rb]"] .roll-text {
  flex-grow: 1;
  background-color: white;
  border: 1px solid #231977;
  border-left: 5px solid #231977;
  border-radius: 7px;
  padding: 5px 15px 5px 15px;
  height: 35px;
}

[cell-rm^="[-rm"] .active .icon {
  width: 35px;
  height: 35px;
  background-color: #231977;
  border-radius: 5px;
}

[cell-rm^="[-rm"] .active .icon i {
  color: white;
  position: absolute;
  margin-top: 9px;
  margin-left: 10px;
}

[cell-rm^="[-rm"] .active .icon .plus {
  display: none !important;
}

[cell-rm^="[-rm"] .active .icon .minus {
  display: block !important;
}

[cell-rm^="[-rm"] .active .roll-text {
  border-left: 1px solid #231977 !important;
}

